.example-container {
  width: 100%;
}
.example-container .anchor {
  display: inline-block;
  margin-right: 50px;
  background-color: red;
  color: white;
}
.example-container .test-area {
  position: relative;
  margin: 20px 100px;
  width: 50%;
  height: 300px;
  border: 1px solid gray;
}
.example-container .lt {
  left: 0px;
  top: 0px;
}
.example-container .rt {
  right: 0px;
  top: 0px;
}
.example-container .lb {
  left: 0px;
  bottom: 0px;
}
.example-container .rb {
  right: 0px;
  bottom: 0px;
}
.example-container .center {
  top: 100px;
  left: 100px;
}



